.row_product_detail{
    padding: 20px 0;
}

.big_img{
    width: 100%;
    padding-top: 90%;
    position: relative;
    margin-bottom: 10px;
    overflow: hidden;
}

.big_img_main{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}

.wrap_thumb_item{
    width: 100%;
    padding-top: 20%;
    position: relative;
    cursor: pointer;
}

.thumb_item{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.color_item_select{
    border: 1px solid rgb(184, 184, 184)!important;
    color: #4e4e4e!important;
    border-radius: 999px!important;
    height: 30px!important;
}

.color_item_select_active{
   transform: translateY(-4px)!important;
}

.wrap_select_img{
    display: flex;
}

.wrap_select_img li{
    padding-right: 4px;
}

.wrap_size{
    display: flex;
}

.size_select_item{
    border: 1px solid rgb(184, 184, 184)!important;
    color: #4e4e4e!important;
    border-radius: 999px!important;
    margin-right: 4px!important;
}

.size_select_item_active{
    background-color: black!important;
    color: white!important;
}

.wrap_select_type{
    padding-bottom: 15px;
}

.wrap_select_type_title{
    display: inline-block;
    padding-bottom: 10px;
    color: #4f4f4f;
}

.wrap_select_total{
    display: inline-block;
    border: 1px solid rgb(212, 212, 212);
    border-radius: 999px;
    height: 40px;
}

.wrap_select_total button{
    width: 40px;
    height: 100%;
    font-size: 17px;
    cursor: pointer;
    margin: 0;
    padding: 0;
    background-color: transparent;
}

.wrap_select_total input{
    width: 70px;
    height: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
    outline: none;
    border: none;
    font-size: 17px;
}

.name{
    font-size: 30px;
    letter-spacing: 1.2px;
    font-weight: 500;
}

.main_price{
    font-size: 40px;
    letter-spacing: 1.2px;
}

.del_price{
    color: #5d5d5d;
    font-weight: 300;
    padding-left: 10px;
}

.size_tutorial{
    display: flex;
    align-items: center;
}

.size_tutorial span{
    padding-left: 10px;
    color: #3f3f3f;
}

.button_size_tutorial{
    margin-left: 20px;
    display: flex;
    align-items: center;
}

.wrap_other{
    padding: 20px 0 50px 0;
}

.wrap_other_product{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}

.wrap_tab{
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
}

.wrap_tab div{
    font-size: 30px;
    font-weight: 500;
    padding-right: 30px;
    color: #777777;
    cursor: pointer;
}

.wrap_tab div.active{
    color: black!important;
}

.buy_button{
    display: inline-block;
    padding: 10px 20px;
    border-radius: 999px;
    margin-right: 5px;
    cursor: pointer;
}

.buy_button div{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.buy_button span{
    padding-left: 5px;
}

.add_to_cart_button{
    border: 1px solid #bcbcbc;
    color: #4f4f4f;
    user-select: none;
}

.buy_now_button{
    background-color: rgb(227, 32, 32);
    color: white;
}

.cal_now_button{
    background-color: black;
    color: white
}

.hr_total{
    margin: 0;
    padding: 0;
    margin-bottom: 15px;
}

@media screen and (max-width: 1024px) {
    .wrap_tab div{
        font-size: 25px;
    }
}

@media screen and (max-width: 992px) {
    .col_right{
        padding-top: 15px;
    }
}

@media screen and (max-width: 768px) {
    .wrap_tab div{
        font-size: 20px;
    }

    .wrap_other_product{
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 561px) {
    .name{
        font-size: 25px;
    }

    .main_price{
        font-size: 30px;
    }
}

@media screen and (max-width: 480px) {
    .buy_button{
        margin-right: 0;
        margin-bottom: 10px;
        width: 100%;
    }

    .buy_button div{
        justify-content: center;
    }

    .wrap_other{
        padding-top: 0;
    }
}

@media screen and (max-width: 389px) {
    .button_size_tutorial{
        width: 100%;
        margin-left: 0;
        margin-top: 15px;
    }
}

@media screen and (max-width: 375px) {
    .wrap_other_product{
        grid-template-columns: 1fr;
    }
}