.footer_top{
    background-color: black;
    padding: 40px 0;
}

.title_footer_top{
    color: white;
    font-size: 20px;
    text-transform: uppercase;
}

.content_footer_top{
    color: white;
    padding-top: 10px;
}

.item_footer_top{
    padding-bottom: 20px;
}

.item_left{
    padding-bottom: 20px;
}

.item_left:last-child{
    padding-bottom: 0;
}

.wrap_link_item{
    display: flex;
    padding-bottom: 15px;
}

.link_item{
    padding-right: 15px;
    padding-top: 15px;
    transition: .5s;
}

.link_item:hover{
    transform: translateY(-5px);
}

.link_item a{
    display: block;
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 999px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.link_item img{
    width: 50%;
}

.form_email_receive{
    width: 100%;
    height: 40px;
    background-color: white;
    margin-top: 10px;
    position: relative;
}

.form_email_receive input{
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    font-size: 16px;
    padding: 0 40px 0 20px;
}

.form_email_receive button{
    width: 10%;
    height: 100%;
    position: absolute;
    z-index: 2;
    cursor: pointer;
    background-color: transparent;
    right: 0;
    transition: .5s;
}

.form_email_receive button:hover{
    transform: translate(5px, -5px);
}

.footer_main{
    padding: 25px 0;
}

.wrap_logo{
    padding-bottom: 15px;
}

.wrap_logo img{
    width: 170px;
}

.title_main_menu{
    font-size: 24px;
    font-weight: 500;
    padding-bottom: 4px;
}

.footer_main_item div,
.footer_main_item a{
    padding: 5px 0;
    color: #404040;
    font-size: 15px;
    cursor: pointer;
}

.footer_bottom{
    text-align: center;
    font-size: 14px;
    padding: 10px 0;
    background-color: rgb(91, 91, 91);
    color: white;
}

@media screen and (max-width: 992px) {
    .col_right{
        padding-top: 20px;
    }
}