.container_cart_page{
    padding-top: 20px;
    padding-bottom: 50px;
}

.product_img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.wrap_product_img{
    width: 100px;
    padding-top: 50%;
    position: relative;
    margin: auto;
}

.title_cart_page{
    font-size: 25px;
    padding-bottom: 20px;
}

.cart_table_desk_top,
.cart_table_desk_top th,
.cart_table_desk_top td{
    border: 1px solid rgb(237, 237, 237);
}

.cart_table_desk_top{
    width: 100%;
}

.cart_table_desk_top th{
    padding: 10px 0;
    text-align: center;
    font-weight: 400;
    color: #272727;
    font-size: 15px;
}

.cart_table_desk_top td{
    text-align: center;
    vertical-align: middle;
    padding: 15px 0;
    font-size: 14px;
}

.wrap_total{
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.continute{
    padding: 10px 25px;
    background-color: rgba(231, 231, 231, 0.667);
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
}

.main_total,
.main_total td{
    border: 1px solid rgba(231, 231, 231, 0.667);
    padding: 10px 15px;
}

.main_total{
    width: 450px;
}

.main_total td:last-child{
    text-align: right;
}

.wrap_button_pay{
    display: flex;
    justify-content: flex-end;
}

.wrap_button_pay button{
    width: 450px;
    padding: 15px;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    color: white;
    background-color: black;
    outline: none;
    cursor: pointer;
}

.remove_item_button{
    cursor: pointer;
    width: 25px;
    background-color: transparent;
}

.wrap_mobile_cart{
    display: none;
}

.wrap_mobile_img{
    width: 15%;
    padding-top: 15%;
    position: relative;
}

.wrap_mobile_img img{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.item{
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
}

.item .name{
    font-size: 13px;
}

.item .price{
    font-size: 13px;
}

.mobile_info{
    width: 60%;
    text-align: left;
    padding-left: 10px;
}

.mobile_total{
    width: 25%;
    text-align: center;
}

.mobile_remove{
    padding: 4px;
    margin-top: 4px;
    background-color: transparent;
    font-size: 14px;
}

.color_item{
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 999px;
    margin-left: 4px;
}

.disabled{
    opacity: .7;
}

@media screen and (max-width: 1024px) {
    .cart_table_desk_top{
        display: none;
    }

    .wrap_mobile_cart{
        display: block;
    }
}

@media screen and (max-width: 768px) {
    .title_cart_page{
        font-size: 20px;
    }

    .continute{
        order: 2;
        width: 100%;
    }
    
    .main_total{
        width: 100%;
        order: 1;
        margin-bottom: 10px;
    }

    .wrap_button_pay button{
        width: 100%;
    }

    .wrap_total{
        padding: 10px 0;
    }
}

@media screen and (max-width: 375px) {
    .wrap_mobile_img{
        display: none;
    }
}