.category_item_left{
    width: 100%;
    height: 500px;
    position: relative;
}

.category_item_left::before,
.category_item_right::before{
    content: "";
    width: 100%;
    height: 100%;
    border: 0px solid transparent;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: .5s;
    cursor: pointer;
}

.category_item_left:hover::before,
.category_item_right:hover::before{
    width: 95%;
    height: 95%;
    border: 3px solid rgb(255, 255, 255);
}

.category_item_right{
    width: 100%;
    height: 250px;
    position: relative;
}

.category_item_right:first-child{
    padding-bottom: 15px;
}

.category_item_right:last-child{
    padding-top: 15px;
}

.category_item_left img,
.category_item_right img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

@media screen and (max-width: 992px) {
    .category_item_right:first-child{
        padding-top: 30px;
    }
}