.wrap_total_product input,
.wrap_total_product button{
    height: 25px;
}

.wrap_total_product button{
    width: 25px;
    cursor: pointer;
    transform: .4s;
}

.wrap_total_product button:hover{
    background-color: black;
    color: white;
}

.wrap_total_product input{
    width: 50px;
    border: 1px solid #dedede;
    outline: none;
    text-align: center;
    font-size: 13px;
    padding: 0 2px;
}

@media screen and (max-width: 426px){
    .wrap_total_product input,
    .wrap_total_product button{
        width: 20px;
        height: 20px;
    }
}