.wrap_all_container{
    padding-top: 20px;
    padding-bottom: 40px;
}

.title{
    font-size: 25px;
    font-weight: 500;
    color: #353535;
}

.wrap_author{
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px dashed rgba(184, 184, 184, 0.667);
}

.item_author{
    font-size: 14px;
    color: rgb(96, 96, 96);
    padding-right: 10px;
}

.item_author span{
    padding-left: 5px;
}

.wrap_all_content{
    padding: 15px 0;
}

@media screen and (max-width: 574px) {
    .title{
        font-size: 20px;
    }
}