.wrap_other,
.wrap_blog_card,
.wrap_new_arrival{
    display: grid;
    grid-gap: 20px;
}

.wrap_slider,
.wrap_new_arrival,
.wrap_blog_card,
.wrap_other,
.wrap_category{
    padding-bottom: 50px;
}

.wrap_new_arrival{
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.wrap_other{
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.wrap_blog_card{
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

@media screen and (max-width: 1024px) {
    .wrap_blog_card{
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 769px) {
    .wrap_slider,
    .wrap_new_arrival,
    .wrap_blog_card,
    .wrap_other,
    .wrap_category{
        padding-bottom: 20px;
    }

    .wrap_new_arrival{
        grid-template-columns: 1fr 1fr;
    }

    .wrap_other{
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 490px) {
    .wrap_blog_card{
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 376px) {
    .wrap_new_arrival{
        grid-template-columns: 1fr;
    }

    .wrap_other{
        grid-template-columns: 1fr;
    }
}