.nav_header {
    box-shadow: 0px 4px 7px #0000000a;
    user-select: none;
    background-color: white;
}

.header_main {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.wrap_logo {
    cursor: pointer;
}

.wrap_logo img{
    width: 170px;
}

.menu_items {
    height: 100%;
}

.menu_items ul {
    display: flex;
    align-items: center;
    list-style-type: none;
    height: 100%;
}

.menu_item {
    padding: 0 6px;
    margin: 0 4px;
    text-transform: uppercase;
    user-select: none;
    cursor: pointer;
    transition: .3s;
    position: relative;
    height: 100%;
}

.menu_item_top {
    display: flex;
    align-items: center;
    height: 100%;
}

.menu_item::before {
    content: "";
    width: 0;
    height: 3px;
    background-color: black;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: .5s;
}

.menu_item:hover::before {
    width: 100% !important;
}

.menu_item:hover .menu_item_bottom {
    display: block;
    width: 250px;
}

.menu_angle_down {
    padding-left: 4px;
    font-size: 11px;
}

.menu_item_top {
    display: flex;
    align-items: center;
}

.menu_item_bottom {
    position: absolute;
    top: 100%;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.113);
    left: 0;
    z-index: 10;
    display: none;
}

.menu_bottom_item {
    width: 100%;
    text-align: left;
}

.menu_bottom_item div{
    padding: 9px 15px;
    width: 100%;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.162);
}

.menu_bottom_item:last-child {
    border-bottom: none;
}

.menu_bottom_item:hover {
    background-color: #f1f1f1;
}

.menu_item_bottom ul {
    display: flex;
    flex-direction: column;
}

.wrap_head_control {
    height: 100%;
    display: flex;
    align-items: center;
}

.search_box {
    position: relative;
    height: 100%;
    width: 50px;
}

.search_box:hover .search_box_input,
.search_box:hover .menu_user {
    display: block;
}

.search_box_icon {
    width: 40px;
    height: 40px;
    border: 1px solid #dddddd;
    border-radius: 999px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.search_box_input {
    position: absolute;
    top: 100%;
    right: 0;
    background-color: white;
    padding: 15px;
    box-shadow: 0 0 7px #00000014;
    display: none;
    z-index: 2;
}

.wrap_search_input {
    display: flex;
    height: 40px;
    border: 1px solid rgba(0, 0, 0, 0.086);
    border-radius: 999px;
    overflow: hidden;
}

.wrap_search_input input {
    border: none;
    outline: none;
    padding: 0 15px;
}

.wrap_search_input span {
    width: 40px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu_user {
    position: absolute;
    top: 100%;
    box-shadow: 0 0 7px #00000014;
    right: 0;
    width: 250%;
    display: none;
    z-index: 2;
    background-color: white;
}

.menu_user ul {
    list-style-type: none;
}

.menu_user ul li {
    padding: 10px 15px;
    border-bottom: 1px dashed #00000028;
    cursor: pointer;
}

.menu_user ul li:last-child {
    border-bottom: 0;
}

.menu_user ul li:hover {
    background-color: #f3f3f3;
}

.card_header {
    display: flex;
    align-items: center;
    padding: 0 5px;
    cursor: pointer;
}

.card_header_left {
    padding-right: 10px;
    font-size: 25px;
}

.card_header_right {
    font-size: 15px;
}

.mobile_header_menu {
    display: none;
}

.create_bill_btn{
    padding: 4px 15px;
    background-color: #005d7b;
    color: white;
}

.create_bill_btn_disable{
    opacity: .8;
}

@media screen and (max-width: 1087px) {
    .wrap_logo img {
        width: 150px;
    }
}

@media screen and (max-width: 1024px) {
    .menu_items,
    .wrap_head_control {
        display: none;
    }

    .header_main {
        justify-content: space-between;
    }

    .mobile_header_menu {
        display: flex;
    }

    .mobile_search_icon {
        padding-right: 15px;
        position: relative;
    }

    .cart_total_num{
        font-size: 11px;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(0, -50%);
        width: 15px;
        height: 15px;
        background-color: red;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        border-radius: 999px;
    }

    .mobile_header_menu {
        display: flex;
    }

    .mobile_search_icon {
        font-size: 18px;
    }

    .mobile_menu {
        width: 100%;
        height: 100vh;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 999;
        transition: .3s;
    }

    .show_menu{
        opacity: 1;
        visibility: visible;
    }
    
    .hide_menu{
        opacity: 0;
        visibility: hidden;
    }

    .main_menu {
        width: 40%;
        height: 100vh;
        position: absolute;
        left: 0;
        top: 0;
        background-color: #ffffff;
        padding: 20px;
        z-index: 2;
        left: -100%;
        transition: .4s;
    }

    .show_main_menu{
        left: 0!important;
    }

    .background_close_menu{
        width: 100%;
        height: 100vh;
        position: absolute;
        z-index: 1;
        background-color: rgba(0, 0, 0, 0.588);
        backdrop-filter: blur(1px);
    }

    .wrap_mobile_menu_logo{
        padding-bottom: 20px;
    }

    .wrap_mobile_menu_logo img{
        width: 150px;
    }

    .mobile_menu_item {
        font-size: 20px;
        padding: 4px 0;
    }

    .mobile_icons{
        display: flex;
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%);
    }

    .mobile_icons li{
        padding: 0 6px;
        font-size: 20px;
    }

    .empty_box{
        width: 60px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -70%);
    }

    .form_search_mobile{
        padding: 10px 0 15px 0;
        border-bottom: 1px solid rgba(221, 221, 221, 0.667);
    }

    .form_search_mobile input{
        width: 70%;
        height: 30px;
        padding: 0 10px;
        border: none;
        outline: none;
        background-color: #a2a2a2;
        color: white;
    }

    .form_search_mobile input::placeholder{
        color: white;
    }

    .form_search_mobile button{
        height: 30px;
        width: 30%;
    }

    .wrap_search_product,
    .list_product_cart{
        max-height: 65vh;
        padding: 15px 0;
        overflow-y: scroll;
        overflow-x: hidden;
    }
}

@media screen and (max-width: 769px){
    .main_menu{
        width: 70%!important;
    }
}

@media screen and (max-width: 426px){
    .main_menu{
        width: 80%!important;
    }
}

@media screen and (max-width: 376px){
    .main_menu{
        width: 80%!important;
    }
}