.wrap_blog_card {
    cursor: pointer;
}

.wrap_img {
    position: relative;
    padding-top: 65%;
    overflow: hidden;
}

.wrap_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.main_title {
    padding: 15px 0 8px 0;
    border-bottom: 1px dashed rgb(184, 184, 184);
}

.title {
    font-size: 18px;
    font-weight: 500;
    color: #272727;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}

.info {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #5d5d5d;
    padding: 5px 0;
}

.item {
    padding-right: 10px;
}

.info span {
    padding-right: 4px;
}

.sub_title {
    padding-top: 8px;
}

.desc {
    color: #5d5d5d;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
}

@media screen and (max-width: 1024px) {
    .title {
        font-size: 17px;
    }

    .desc {
        font-size: 15px;
    }
}

@media screen and (max-width: 592px) {
    .title {
        font-size: 16px;
    }

    .info {
        font-size: 13px;
    }

    .desc {
        font-size: 14px;
    }
}