.title{
    text-align: center;
    padding-bottom: 35px;
}

.title span{
    font-size: 30px;
    font-weight: 500;
    color: #2a2a2a;
    cursor: pointer;
    user-select: none;
}

.title span:hover{
    color: #676767;
}

@media screen and (max-width: 769px) {
    .title{
        padding-bottom: 15px;
    }
    
    .title span{
        font-size: 22px;
    }
}