.main_product_category{
    padding: 30px 0;
}

.category_input_item{
    display: flex;
    align-items: center;
}

.category_input_item input,
.category_input_item label{
    margin: 0;
}

.category_input_item label{
    padding-left: 5px;
    font-size: 16px;
    color: #717171;
    user-select: none;
}

.category_input_item input{
    width: 17px;
    height: 17px;
    color: black;
}

.title_filter{
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid black;
}

.filter_title_item{
    display: block;
    color: #606060;
    padding-bottom: 5px;
}

.by_category,
.by_color,
.by_size,
.by_price{
    padding-bottom: 15px;
}

.color_item{
    width: 30px;
    height: 30px;
    border: 1px solid #dcdcdc;
    cursor: pointer;
    margin-right: 4px;
    margin-bottom: 4px;
}

.color_item_active{
    position: relative;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 5px;
}

.wrap_color_item_filter{
    display: flex;
    flex-wrap: wrap;
}

.wrap_filter_price{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 10px;
}

.wrap_filter_price span{
    padding: 0 5px;
}

.wrap_filter_price input{
    text-align: center;
    border: 1px solid #d8d8d8;
    outline: none;
    padding: 7px 0;
    width: 45%;
}

.wrap_product{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 15px;
}

.filter_button{
    padding: 10px 25px;
    background-color: black;
    color: white;
    cursor: pointer;
    user-select: none;
}

.wrap_paginate{
    padding: 30px 0 15px 0;
    display: flex;
    justify-content: flex-end;
}

.wrap_order_by{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10px;
    background-color: rgb(239, 239, 239);
    margin-bottom: 15px;
}

.order_by_group{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    user-select: none;
}

.order_by_item{
    padding-left: 15px;
    font-size: 13.5px;
    cursor: pointer;
    color: #5a5a5a;
}

.order_by_item_active{
    font-weight: 600;
    color: black;
}

@media screen and (max-width: 992px) {
    .col_left{
        order: 2;
    }

    .col_right{
        order: 1;
    }
}

@media screen and (max-width: 768px) {
    .wrap_product{
        grid-template-columns: 1fr 1fr;
    }

    .title_order_by{
        display: none!important;
    }
}

@media screen and (max-width: 376px) {
    .wrap_product{
        grid-template-columns: 1fr;
    }

    .wrap_paginate{
        justify-content: center;
    }
}