.input {
    width: 100%;
    height: 40px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #d3d3d3;
    padding: 0 15px;
    font-size: 15px;
    transition: .2s;
}

.input:focus,
.input_text_area:focus {
    outline: 2px solid rgb(0, 137, 234);
}


.wrap_input {
    padding-bottom: 8px;
}

.input_text_area {
    width: 100%;
    height: 80px;
    border: 1px solid #d3d3d3;
    border-radius: 4px;
    padding: 10px;
    font-size: 15px;
}

label {
    font-size: 14px;
    color: #525252;
    margin-bottom: 5px;
}

label span {
    color: red;
}

.col_right,
.col_left {
    padding-top: 20px;
    padding-bottom: 50px;
}

.col_right{
    padding-left: 50px;
}

.col_left{
    padding-right: 50px;
}

.title_left {
    padding-bottom: 15px;
}

.trans {
    width: 100%;
    height: 60px;
    border: 1px solid #d3d3d3;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
}

.trans span {
    display: block;
    width: 20px;
    height: 20px;
    background-color: rgb(0, 99, 152);
    border-radius: 999px;
    position: relative;
    margin-right: 10px;
}

.trans span::before {
    content: "";
    width: 4px;
    height: 4px;
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: white;
    transform: translate(-50%, -50%);
    border-radius: 999px;
}

.wrap_item {
    padding-bottom: 15px;
}

.wrap_product_item {
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.wrap_product_item:hover {
    background-color: rgb(245, 245, 245);
}

.main_img {
    width: 17%;
}

.wrap_img {
    width: 60px;
    height: 60px;
    position: relative;
}

.wrap_img img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.wrap_img span {
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 83, 138);
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
    font-size: 10px;
    border-radius: 999px;
    color: white;
}

.info_product_item {
    display: flex;
    justify-content: space-between;
    width: 83%;
}

.product_name {
    text-transform: uppercase;
    color: #3b3b3b;
}

.detail {
    font-size: 14px;
    color: #525252;
}

.wrap_items {
    max-height: 220px;
    overflow-x: hidden;
    overflow-y: scroll;
    padding-right: 5px;
    margin-bottom: 15px;
}


.wrap_items::-webkit-scrollbar {
    width: 5px;
}

.wrap_items::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.wrap_items::-webkit-scrollbar-thumb {
    background: rgb(230, 230, 230);
    border-radius: 999px;
}

.pay_total_info{
    padding-top: 15px;
}

.item_pay_info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 15px;
    padding-bottom: 5px;
}

.item_pay_info span:first-child{
    font-weight: 500;
    color: #3e3e3e;
}

.wrap_total_all{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.wrap_total_all span:first-child{
    font-size: 20px;
}

.wrap_total_all span:last-child{
    font-size: 28px;
    color: #0089d9;
}

.wrap_button_pay{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.button_back{
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 15px;
    color: #0089d9;
}

.button_back div{
    display: block;
    padding-left: 5px;
}

.buy_now{
    padding: 10px 20px;
    color: white;
    background-color: #0075b9;
    border-radius: 5px;
    cursor: pointer;
}

.buy_now_disabled{
    background-color: #004d7a!important;
    opacity: .7;
}

.buy_now:hover{
    background-color: #004d7a!important;
}

.col_right::before{
    content: "";
    width: 200%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgb(243, 243, 243);
    z-index: -1;
}

.order_page{
    overflow: hidden;
}

@media screen and (max-width: 1024px) {
    .col_right::before{
        width: 200%;
        left: 50%;
        transform: translateX(-50%);
    }
    .col_right{
        padding-left: 15px;
    }

    .col_left{
        padding-right: 15px;
    }
}

@media screen and (max-width: 524px) {
    .main_img{
        width: 20%;
    }
    .wrap_img{
        width: 40px;
        height: 40px;
    }

    .wrap_img img{
        border-radius: 2px;
    }

    .wrap_img span{
        width: 15px;
        height: 15px;
    }

    .right_price_item{
        font-size: 14px;
    }

    .product_name,
    .detail{
        font-size: 14px;
    }
}

.wrap_input_error input,
.wrap_input_error textarea{
    outline: 1px solid red!important;
}

.err_msg{
    font-size: 13px;
    color: red;
    padding-top: 2px;
}