.container_all{
    padding: 20px 0;
}

.wrap_blog_card{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
}

.wrap_paginate{
    display: flex;
    justify-content: flex-end;
    padding: 30px 0;
}

@media screen and (max-width: 1024px) {
    .col_left{
        order: 2;
    }
}