.slider_wrap_item{
    width: 100%;
    position: relative;
    padding-top: 40%;
    overflow: hidden;
}

.slider_wrap_item img{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    object-fit: cover;
}

