.mobile_product_cart{
    display: flex;
    justify-content: space-between;
    padding: 5px 0;
}

.left{
    width: 20%;
}

.wrap_img{
    width: 50px;
    height: 50px;
}

.left img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.right{
    width: 10%;
}

.center{
    width: 70%;
    padding-left: 15px;
}

.name{
    line-height: 1;
    font-size: 14px;
}

.total button{
    width: 25px;
    height: 25px;
}

.total input{
    width: 50px;
    height: 25px;
    border: 1px solid #c5c5c5;
    outline: none;
    text-align: center;
    padding: 0 4px;
}

.remove{
    background-color: transparent;
    color: red;
    font-size: 15px;
}

.color_box{
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 999px;
    margin-right: 5px;
}

.size_box{
    font-size: 13px;
    color: #333333;
}