.product_card{
    cursor: pointer;
}

.top{
    width: 100%;
    padding-top: 105%;
    position: relative;
    overflow: hidden;
}

.top img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;
}

.bottom{
    padding-top: 25px;
}

.name{
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    color: #3d3d3d;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1; /* number of lines to show */
    line-clamp: 1; 
    -webkit-box-orient: vertical;
}

.price{
    font-size: 17px;
}

.price span{
    padding-right: 4px;
    font-weight: 500;
    color: #393939;
}

.price del{
    color: #949494;
}

.view_button{
    width: 100%;
    position: absolute;
    z-index: 10;
    top: 100%;
    text-align: center;
    color: white;
    padding: 10px 0;
    background-color: rgba(103, 103, 103, 0.535);
    backdrop-filter: blur(1px);
    transition: .3s;
    font-weight: 500;
}

.product_card:hover .view_button{
    transform: translateY(-100%);
}

@media screen and (max-width: 1024px){
    .name{
        font-size: 16px;
    }

    .price{
        font-size: 15px;
    }

    .bottom{
        padding-top: 15px;
    }    
}