.card_category {
    display: flex;
    padding: 5px 0;
    cursor: pointer;
}

.wrap_img_category {
    width: 30%;
    padding-top: 20%;
    overflow: hidden;
    position: relative;
}

.wrap_img_category img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.wrap_content {
    width: 70%;
    padding-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
}

.title_category{
    font-size: 26px;
    font-weight: 500;
    padding-bottom: 5px;
    border-bottom: 1px solid #272727;
    margin-bottom: 10px;
}

@media screen and (max-width: 630px) {
    .title_category{
        font-size: 25px!important;
    }
}

@media screen and (max-width: 494px) {
    .wrap_blog_card{
        grid-template-columns: 1fr;
    }
}